
import api from '../server/api'

const state = {
    comments: [],
    rateTags: []
}

const mutations = {
    setComment(state, data){
        state.comments = data;
    },
    setRateTag(state, data){
        state.rateTags = data;
    }
}

const actions = {
    loadComments({ commit }, pid){
        api.loadComment({
            type:1,
            itemId:pid,
            appPlat:'m'
        }).then(({ data })=>{
            if(!data){
                commit('setComment', []);
                commit('setRateTag', []);
                return;
            }
            commit('setComment', data.list);
            commit('setRateTag', data.rateTags);
        })
    }
}

const getters = {

}

export default {
    state,
    mutations,
    actions,
    getters
}


