<template>
    <div>
        <div class="nav" v-show="flag">
            <div>
                <i class="iconfont icon-xiaoxizhongxin"></i>
            </div>
            <div class="search" @click="changeShow()">
                <i class="iconfont icon-sousuo"></i>
                <span>{{ hot.query }}</span>
            </div>
            <div>
                <i class="iconfont icon-gouwuche" @click="goToCart()"></i>
            </div>    
        </div> 
        <div class="navChange" v-show="!flag">
            <div>
                <i class="iconfont icon-xiangzuojiantou" @click="changeShow()"></i>
            </div>
            <div class="searchInput">
                <form action="">
                    <input type="text" :placeholder="hot.query" v-model="navSearch">
                </form>
            </div>  
            <div @click="beginSearch()">搜索</div>
        </div>   
        <!-- search show start -->
        <transition enter-active-class="animated bounceInUp" leave-active-class="animated bounceOutDown">
            <div v-show="!flag" class="searchList">
                <div class="history">
                    <i class="iconfont icon-sousuo"></i>
                    <span>历史搜索</span>
                    <i class="iconfont icon-lajixiang del"></i>
                    <div class="searchContent" v-show="search">

                    </div>
                    <div class="searchContent" v-show="!len">
                        暂无历史搜索
                    </div>
                    <div class="historyWord" v-show="len">
                        <span v-for="(v,i) of history" :key="'hist'+i">{{ v }}</span>
                    </div>
                </div>
                <div class="hot">
                    <div>
                        <i class="iconfont icon-shoucang"></i>
                        <span>热门搜索</span>
                    </div>
                    <div class="hotsearch">
                        <div v-for="(v,i) of hot.hotList" :key="'hot'+i" :style="{color:v.color}" @click="queryWold(v.query)">
                            {{ v.query }}
                        </div>
                    </div>
                </div>
            </div>
        </transition>
        <!-- search show end -->
    </div>  
</template>

<script>

import { mapState, mapActions, mapMutations, mapGetters } from 'vuex'
export default {
    data(){
        return {
            flag:true,
            isFocus:false,
            search:false,
            navSearch:"",
            history:{}
        }
    },
    computed:{
        len(){
            return Object.keys(this.history).length;
        },
        ...mapState(['hot'])
    },
    props:{
        status:{type:Boolean,default:true}
    },
    watch:{
        status(){
            this.flag = !this.flag;
        }
    },
    methods:{
        changeShow(){
            this.flag = !this.flag;
            this.getHistory();
        },
        goToCart(){
            console.log(111);
            this.$router.push('/shop');
        },
        queryWold(searchWord){
            let hist = JSON.parse(sessionStorage.getItem('historySearch'));
            hist[searchWord] = searchWord;
            sessionStorage.setItem('historySearch', JSON.stringify(hist));
            this.$router.push('/More/'+searchWord);
        },
        beginSearch(){
            let hist = JSON.parse(sessionStorage.getItem('historySearch'));
            hist[this.navSearch] = this.navSearch;
            sessionStorage.setItem('historySearch', JSON.stringify(hist));
            this.$router.push('/More/'+this.navSearch);
        },
        getHistory(){
            let hist = sessionStorage.getItem('historySearch');
            if(hist == undefined){
                hist = {};
                sessionStorage.setItem('historySearch',JSON.stringify(hist));
                return;
            }
            this.history = JSON.parse(hist);
        },
        ...mapActions(['loadHotList'])
        
    },
    mounted(){
        this.getHistory();
        this.loadHotList();
    }
}
</script>


<style lang="less" scoped>
@import url('../../lessVar');
    
    .nav{
        width: 100%;
        height: @topHeight;/*px*/
        line-height: @topHeight;/*px*/
        display: flex;
        justify-content: space-between;
        text-align: center;
        font-size: 40px;/*px*/
        border-bottom: 1px solid #ccc;
        >div{
            width: 15%;
            .iconfont{
                font-size: 50px;/*px*/
            }
        }
        >.search{
            font-size: 30px;/*px*/
            width: 70%;
            margin: 20px 0;/*px*/
            line-height: 60px;/*px*/
            background: #ccc;
            border-radius: 10px;/*px*/
            text-align: left;
            color: #aaa;
            .iconfont{
                font-size: 40px;/*px*/
                margin-left: 20px;/*px*/
            }
        }
    }
    .navChange{
        width: 100%;
        height: @topHeight;/*px*/
        line-height: @topHeight;/*px*/
        font-size: 40px;/*px*/
        border-bottom: 1px solid #ccc;
        >div{
            float: left;
            margin-left: 20px;/*px*/
        }
        >div:last-child{
            font-size: 30px;/*px*/
            margin-left: 5px;
        }
        .searchInput{
            width: 76%;
            font-size: 30px;/*px*/
            form{
                margin-right: 10px;
                input{
                    height: 50px;/*px*/
                    width: 94%;/*px*/
                    background: #ccc;
                    background: none;
                    outline: none;
                    border-radius: 10px;
                    border: 1px solid #424242;
                    box-shadow: none;
                    vertical-align: middle;
                    padding-left: 20px;/*px*/
                    font-size: 30px;
                }
                input:focus{
                    border: 1px solid @colorful;
                }
            }
        }
        

    }
    .searchList{
        position: absolute;
        top: @topHeight;/*px*/
        bottom: 0px;
        left: 0px;
        right: 0px;
        z-index: 999;
        background: white;
        .history{
            padding: 20px 60px 10px 20px;/*px*/
            font-size: 30px;/*px*/
            color: #888;
            border-bottom: 1px solid #ccc;/*px*/
            >.del{
                float: right;
                font-size: 36px;/*px*/
            }
            >.searchContent{
                padding: 20px;
            }
            .historyWord{
                padding: 10px 20px;
                span{
                    display: inline-block;
                    padding: 5px 10px;
                    margin-right: 10px;
                    border-radius: 10px;
                    border: 1px solid #ccc;
                }
            }
        }
        .hot{
            padding: 20px 60px 10px 20px;/*px*/
            font-size: 30px;/*px*/
            color: #888;
            >.hotsearch{
                padding: 20px 30px;/*px*/
                line-height: 60px;/*px*/
                display: flex;
                flex-wrap: wrap;
                justify-content: flex-start;
                >div{
                    border: 1px solid #ccc;
                    margin-right: 20px;
                    margin-top: 30px;
                    padding: 0px 20px;
                    border-radius: 10px;
                }
            }
        }
    }
</style>



