<template>
    <div class="product" @click="detail()" v-show="like.price">
        <div>
            <!-- <img :src="like.img" alt=""> -->
            <img v-lazy="like.img" alt="">
        </div>
        <slot name="title"></slot>
        <div class="price">
            <slot name="price"></slot>
            <slot name="cfav"></slot>
        </div>
    </div>
</template>

<script>
export default {
    props:{
        like: null
    },
    methods:{
        detail(){
            sessionStorage.setItem("product",JSON.stringify(this.like));
            this.$router.push('/detail');
        }
    }
}
</script>

<style lang="less" scoped>
    .product{
        width: 344px;
        height: auto;
        img{
            width: 100%;
            height: auto;;
        }
        >div:first-child{
            width: 100%;
            height: auto;
            box-sizing: border-box;
            margin: 0 auto;
        }
        & .price{
            font-size: 30px;/*px*/
            height: 50px;/*px*/
            line-height: 50px;/*px*/
            font-weight: bold;
        }
        
    }
</style>



