<template>
    <div ref="wrapper">
        <slot></slot>
    </div>
</template>

<script>

import BScroll from 'better-scroll'

export default {
    props:{
        click: {
            type: Boolean,
            default: true
        },
        scrollX: {
            type: Boolean,
            default: true
        }
    } ,
    methods: {
        _initScroll(){
            if(!this.$refs.wrapper){
                return;
            }
            this.scroll = new BScroll(this.$refs.wrapper,{
                click:this.click,
                scrollX: this.scrollX
            })
        },

    },
    mounted(){
        setTimeout(()=>{
            this._initScroll();
        },20)
    }   
}
</script>

<style lang="less" scoped>

</style>



