import VueAwesomeSwiper from '../../node_modules/vue-awesome-swiper'
import NavComponent from '../components/global/NavComponent'
import FooterComponent from '../components/global/FooterComponent'
import ProductComponent from "../components/global/ProductComponent";
import ScrollComponent from "../components/global/ScrollComponent";

import showData from "../global/showData";

export default {
    install: function(Vue){
        Vue.use(VueAwesomeSwiper);
        Vue.component("NavComponent",NavComponent);
        Vue.component("FooterComponent",FooterComponent);
        Vue.component("ProductComponent",ProductComponent);
        Vue.component("ScrollComponent",ScrollComponent);
        Vue.filter("showTitle",showData.deleteSymbol);
    }
}



