<template>
    <div>
        <div class="my">
            <div class="person">
                <div>
                    <img src="../../static/img/图标/wechat.png" alt="">
                </div>
                <div class="info">
                    <div class="name">
                        {{ name }}
                    </div>
                    <div class="address">
                        我的收货地址<span class="extend">></span>
                    </div>
                </div> 
            </div>
            <div class="order">
                <div class="myOrder">
                    <div class="img">
                        <img src="../../static/img/图标/order.png" alt="">
                    </div>
                    <div class="text">
                        <span>我的订单</span>
                        <span class="extend">></span>
                    </div>
                </div>
            </div>
            <div class="allchoose">
                <div class="myOrder">
                    <div class="img">
                        <img src="../../static/img/图标/cart.png" alt="">
                    </div>
                    <div class="text">
                        <span>我的购物车</span>
                        <span class="extend">></span>
                    </div>
                </div>  
                <div class="myOrder">
                    <div class="img">
                        <img src="../../static/img/图标/pintuan.png" alt="">
                    </div>
                    <div class="text">
                        <span>我的拼团</span>
                        <span class="extend">></span>
                    </div>
                </div>    
                <div class="myOrder">
                    <div class="img">
                        <img src="../../static/img/图标/qianbao.png" alt="">
                    </div>
                    <div class="text">
                        <span>我的钱包</span>
                        <span class="extend"><span class="getMoney">快速提现</span> ></span>
                    </div>
                </div>    
                <div class="myOrder">
                    <div class="img">
                        <img src="../../static/img/图标/youhuiquan.png" alt="">
                    </div>
                    <div class="text">
                        <span>我的优惠券</span>
                        <span class="extend">></span>
                    </div>
                </div>    
                <div class="myOrder">
                    <div class="img">
                        <img src="../../static/img/图标/product.png" alt="">
                    </div>
                    <div class="text">
                        <span>我收藏的商品</span>
                        <span class="extend">></span>
                    </div>
                </div>    
                <div class="myOrder">
                    <div class="img">
                        <img src="../../static/img/图标/shop.png" alt="">
                    </div>
                    <div class="text shop">
                        <span>我收藏的店铺</span>
                        <span class="extend">></span>
                    </div>
                </div>      
            </div> 
            <div>
                <div class="myOrder">
                    <div class="img">
                        <img src="../../static/img/图标/wechat.png" alt="">
                    </div>
                    <div class="text">
                        <span>消息通知</span>
                        <span class="extend">></span>
                    </div>
                </div>  
                <div class="myOrder">
                    <div class="img">
                        <img src="../../static/img/图标/kefu.png" alt="">
                    </div>
                    <div class="text">
                        <span>客服</span>
                        <span class="extend">></span>
                    </div>
                </div>  
                <div class="myOrder">
                    <div class="img">
                        <img src="../../static/img/图标/yijian.png" alt="">
                    </div>
                    <div class="text yijian">
                        <span>意见反馈</span>
                        <span class="extend">></span>
                    </div>
                </div>  
            </div>
            <div class="btn">
                <input type="button" value="退出登录" @click="out()">
            </div>   
        </div>
        <footer-component></footer-component>
    </div>
</template>

<script>
export default {
    data(){
        return {
            name:''
        }
    },
    methods:{
        out(){
            sessionStorage.removeItem('historySearch');
            sessionStorage.removeItem('isLogin');
            this.$router.push('/');
        }
    },
    mounted(){
        let uname = sessionStorage.getItem('user');
        this.name = uname;
    }
}
</script>

<style lang="less" scoped>
    @import url('../lessVar');
    .my{
        position: absolute;
        top: 0px;
        bottom: @bottomHeight;
        left: 0px;
        right: 0px;
        padding: 10px;
        background: #eee;
        .person{
            background: white;
            padding-left: 30px;
            margin-bottom: 20px;
            display: flex;
            height: 120px;
            vertical-align: middle;
            img{
                width: 90px;
                margin-top: 15px;
                margin-right: 30px;
            }
            .info{
                margin-top: 16px;
                >div{
                    width: 100%;
                }
                >.name{
                    font-size: 30px;/*px*/
                }
            }
            .address{
                .extend{
                    margin-left: 50px;
                }
            }
        }
        .order{
            border-bottom: 1px solid #ccc;
            margin-bottom: 20px;
            >.myOrder{
                border-bottom: none;
            }
        }
        .myOrder{
            background: white;
            padding-left: 30px;
            height: 90px;
            line-height: 90px;
            font-size: 30px;/*px*/
            color: #666;
            display: flex;
            >.text{
                position: absolute;
                right: 10px;
                width: 82%;
                margin-right: 0px;
                border-bottom: 1px solid #ccc;
                >.extend{
                    float: right;
                    margin-right: 40px;
                    color: #aaa;
                    font-size: 40px;/*px*/
                    >.getMoney{
                        font-size: 24px;/*px*/
                    }
                    
                }
            }
            img{
                width: 55px;
                vertical-align: middle;
            }
            .yijian{
                border-bottom: none;
            }
        }
        .allchoose{
            margin-bottom: 20px;
            .shop{
                border-bottom: none;
            }

        }
        .btn{
            width: 80%;
            margin: 20px auto;
            input{
                font-size: 30px;
                color: white;
                width: 100%;
                height: 80px;/*px*/
                background: @colorful;
                border-radius: 10px;
            }
        }
    }
</style>




