<template>
    <div class="shop">
        <div class="top">
            <div class="goBack" @click="gotoHome()">
                <i class="iconfont icon-xiangzuojiantou"></i>
            </div>
            <div class="cart">购物车({{ Object.keys(products).length }})</div>
            <div class="change">
                <span>编辑</span>
                <i class="iconfont icon-xiaoxizhongxin"></i>
            </div>
        </div>
        <div class="products">
            <scroll-component class="wrapper">    
                <div class="content">
                    <div class="clear"></div>
                    <div v-for="(v,i) of products" :key="i">
                        <div class="product">
                            <div class="choose">
                                <input type="checkbox" v-model="v.status" @change="choose()">
                            </div>
                            <div class="img">
                                <img :src="v.img" alt="">
                            </div>
                            <div class="info">
                                <p class="title">{{ v.title }}</p>
                                <p class="price">￥{{ v.price }}</p>
                                <p class="num">X {{ v.num }}</p>
                            </div>
                        </div>
                    </div>
                </div>
            </scroll-component>
        </div>
        <div class="buy">
            <div class="choose">
                <input type="checkbox" name="" id="chooseAll" v-model="flag" @click="chooseAll()">
                <label for="chooseAll">全选（{{ buyCount }}）</label>
            </div>
            <div class="money">
                {{ getMoney }}
            </div>
            <div class="toBuy">
                <input type="button" value="去结算">
            </div>
        </div>
    </div>
</template>

<script>

import BScroll from 'better-scroll'

export default {
    data(){
        return {
            buyCount: 0,
            buyMoney: 0,
            obj:[
                {
                    title:'title',
                    price:90,
                },
                {
                    title:'title',
                    price:90,
                }
            ],
            flag:false,
            products:{}
        }
    },
    computed:{
        getMoney(){
            return '￥'+this.buyMoney.toFixed(2);
        }
    },
    methods:{
        chooseAll(){
            this.flag = !this.flag;
            this.buyMoney = 0;
            this.buyCount = 0;
            let sum = 0;
            let count = 0;
            for (const i in this.products) {
                this.products[i]['status'] = this.flag;
                sum += this.products[i]['num'] * this.products[i]['price'];
                count += this.products[i]['num'];
            }
            if(this.flag){
                this.buyMoney = sum;
                this.buyCount = count;
                return;
            }
        },
        choose(){
            let sum = 0;
            let count = 0;
            this.flag = true;
            for (const i in this.products) {
                if(!this.products[i]['status']){
                    this.flag = false;
                }else{
                    sum += this.products[i]['num']*this.products[i]['price'];
                    count += this.products[i]['num'];
                }
            }
            this.buyCount = count;
            this.buyMoney = sum;
        },
        gotoHome(){
            this.$router.push('/');
        }
    },
    mounted(){
        let user = sessionStorage.getItem('user');
        let shops = localStorage.getItem(user);
        if(shops==undefined){
            this.products = {};
            return;
        }
        this.products = JSON.parse(shops);
    }
}
</script>

<style lang="less" scoped>
    @import url('../lessVar');
    .shop{
        .top{
            position: fixed;
            top:0px;
            height: @topHeight;
            line-height: @topHeight;
            display: flex;
            justify-content: flex-start;
            width: 100%;
            text-align: center;
            font-size: 40px;/*px*/
            color:#888;
            border-bottom: 1px solid #aaa;
            >.goBack{
                width: 10%;
            }
            >.cart{
                width: 70%;
            }
            >.change{
                width: 20%;
                font-size: 30px;/*px*/
                .iconfont{
                    font-size: 36px;/*px*/
                    margin-left: 10px;/*px*/
                }
            }
        }
        .products{
            position: absolute;
            top:@topHeight;
            bottom: @bottomHeight;
            left: 0px;
            right: 0px;
            background: #eee;
            .wrapper{
                width: 100%;
                height: 100%;
            }
            .clear{
                width: 100%;
                height: 30px;/*px*/
                background: #eee;
            }
            .product{
                padding: 10px 30px;
                margin-bottom: 30px;
                background: white;
                >div{
                    display: inline-block;
                    vertical-align: top;
                    padding-top: 10px;
                    height: 220px;
                }
                .choose{
                    width: 5%;
                    position: relative;
                    top:80px;
                }
                .img{
                    width: 20%;
                    img{
                        width: 100%;
                    }
                }
                .info{
                    width: 70%;
                    padding-left: 10px;
                    position: absolute;
                    .title{
                        font-size: 24px;/*px*/
                    }
                    .price{
                        position: absolute;
                        bottom: 10px;
                    }
                    .num{
                        position: absolute;
                        bottom: 10px;
                        right: 30px;;
                    }
                }
                input{
                    width: 30px;/*px*/
                    height: 30px;/*px*/
                }
            }
            .market{
                background: white;
                padding: 10px 30px;
                border-top: 1px solid #ccc;
                input{
                    width: 30px;/*px*/
                    height: 30px;/*px*/
                    background: white;
                }
            }
        }
        .buy{
            position: fixed;
            bottom: 0px;
            height: @bottomHeight;
            line-height: @bottomHeight;
            left: 0px;
            right: 0px;
            border-top: 1px solid #ccc;
            >div{
                display: inline-block;
            }
            >.choose{
                font-size: 28px;/*px*/
                margin-left: 20px;
                input{
                    width: 30px;
                    height: 30px;;
                }
            }
            >.money{
                color:@colorful;
            }
            >.toBuy{
                margin-right: 30px;
                float: right;
                input{
                    width: 200px;
                    height: 60px;/*px*/
                    background: @colorful;
                    font-size: 30px;/*px*/
                    color: white;
                    border-radius: 10px;
                }
            }
        }
    }
</style>




