import Vue from 'vue'
import Router from 'vue-router'
// import ContentComponent from '../components/ContentComponent';
import HomeComponent from "../components/HomeComponent";
import CartComponent from "../components/CartComponent";
import ClassifyComponent from "../components/ClassifyComponent";
import MeComponent from "../components/MeComponent";
import MoreComponent from "../components/MoreComponent";
import LoginComponent from "../components/LoginComponent";

import DetailProduct from "../components/global/DetailProduct";

Vue.use(Router)

const router =  new Router({
  mode:'history',
  linkActiveClass:'active',
  linkExactActiveClass:'active-exact',
  routes: [
    {path: '/',component: HomeComponent},
    {path: '/shop',component: CartComponent},
    {path: '/classify',component: ClassifyComponent},
    {path: '/me',component: MeComponent},
    {path: '/detail',component: DetailProduct, props:true},
    {path:'/more/:word/:area?',component:MoreComponent},
    {path:'/login',component: LoginComponent}
  ]
})

const isLoginPage = ['/me','/shop'];
router.beforeEach((to, from, next)=>{
  const isLogin = sessionStorage.getItem('isLogin');
  if(isLoginPage.indexOf(to.path)!=-1&&!isLogin){
    sessionStorage.setItem('backpath',to.path);
    next("/login");
    return;
  }
  next();
});

export default router;



