import api from '../server/api'

const state = {
    hotList: [],
    query:""
}

const actions = {
    loadHotList({ commit }){
        api.loadHot({
            code:'sketch,hotWord',
            platform:'H5'
        }).then(({ data })=>{
            commit('setHotList',data.hotWord.data);
            commit('setQuery',data.sketch.data.query);
        })
    }
}

const mutations = {
    setHotList(state, data){
        state.hotList = data;
    },
    setQuery(state, data){
        state.query = data;
    }
}

const getters = {

}

export default {
    state,
    actions,
    mutations,
    getters
}


