
import api from '../server/api'

const state = {
    leftTypes: [],
    rightTypes: [],
    topTypes: [],
    contentTypes: [],
    colorObj: {},
    currentId:'',
    typeId:''
}

const mutations = {
    setLeftTypes(state, data){
        state.leftTypes = data;
    },
    setRightTypes(state, data){
        state.rightTypes = data;
    },
    setTopTypes(state, data){
        state.topTypes = data;
    },
    setContentTypes(state, data){
        state.contentTypes = data;
    },
    setCurrentId(state, data){
        state.currentId = data;
    },
    setTypeId(state, data){
        state.typeId = data;
    }
}

const actions = {
    loadLeftData({ commit }){
        api.loadDatasByPids({
            appPlat: 'm',
            pids: '41789,'
        }).then(( {data} )=>{
            commit('setTypeId', data[41789].list[0].maitKey);
            commit('setCurrentId',data[41789].list[0].miniWallkey);
            commit('setLeftTypes',data[41789].list);
        })
    },
    loadRightType({ commit, state }, { id }){
        commit('setRightTypes', []);
        id = id || state.typeId;
        api.loadRightType({
            appPlat: 'm',
            pid: id
        }).then(( {data} )=>{
            commit('setTypeId', id);
            commit('setRightTypes', data.categoryNavigation.list);
        })
    },
    loadTopTypes({ commit, state }, { miniKey, sortKey="",callback=()=>{} }){
        commit('setContentTypes', []);
        miniKey = miniKey || state.currentId;
        api.loadGuessData({
            fcid:miniKey,
            page:1,
            sort:sortKey,
            cKey:'h5-cube'
        }).then((data)=>{
            commit('setTopTypes', data.result.sortFilter);
            commit('setContentTypes',data.result.wall.docs);
            commit('setCurrentId',miniKey);
            callback();
        })
    }

}

const getters = {

}


export default {
    state,
    mutations,
    actions,
    getters
}


