<template>
    <div class="detail">
        <div class="back" @click="$router.go(-1)">&lt;</div>
        <scroll-component class="wrapper">    
            <div class="content">
                <!-- swiper start -->
                <div class="wrapSwiper">
                    <swiper :options="swiperOption">
                        <swiper-slide class="swiper-slide" v-for="(v,i) of 5" :key="'img'+i">
                            <img :src="img">
                        </swiper-slide>  
                        <div class="swiper-pagination"></div>     
                    </swiper>
                </div>
                <!-- swiper end -->
                <div class="title">
                    {{ title }}
                </div> 
                <div class="price">
                    <span>￥</span><span>{{ price }}</span>
                    <div v-show="flag">
                        <div class="old">￥{{ oldprice }}</div>
                        <div class="sale">限量促销</div>
                    </div>
                </div>
                <input type="button" value="加入购物车" class="btn" @click="addInShop()">
                <div class="comment">
                    <div class="desc" v-show="detail.rateTags.length">
                        <span v-for="(v,i) of detail.rateTags" :key="'rate'+i">{{v.value}}({{v.num}})</span>
                    </div>
                    <div v-for="(v,i) of detail.comments" :key="'comment'+i">
                        <div class="avatar">
                            <img :src="v.user.avatar" alt="">
                            <span>{{ v.user.uname }}</span>
                        </div>
                        <div class="content">
                            {{ v.content }}
                        </div>
                        <div>
                            {{ v.time }}
                        </div>
                    </div>
                </div>
            </div>
        </scroll-component> 
    </div>    
</template>

<script>

import BScroll from 'better-scroll'
import { swiper, swiperSlide } from "vue-awesome-swiper";
import { mapState, mapActions, mapMutations, mapGetters } from 'vuex'
export default {
    data(){
        return {
            img:"",
            title:"",
            price:"",
            oldprice:"",
            flag:true,
            swiperOption: {
                autoplay: false,
                effect : 'coverflow',
                slidesPerView: 2,
                centeredSlides: true,
                coverflow: {
                            rotate: 30,
                            stretch: 10,
                            depth: 60,
                            modifier: 2,
                            slideShadows : true
                        }
            }
        }
    },
    computed:{
        ...mapState(['detail'])
    },
    methods:{
        addInShop(){
            let isLogin = sessionStorage.getItem('isLogin');
            if(!isLogin){
                this.$router.push('/login');
                return;
            }
            let uname = sessionStorage.getItem('user');
            let cart = localStorage.getItem(uname);
            if(cart == undefined){
                cart = {};
                let product = JSON.parse(sessionStorage.getItem('product'));
                product['num'] = 1;
                let key = product['tradeItemId'];
                cart[key] = product;
                localStorage.setItem(uname,JSON.stringify(cart));
                return;
            }else{
                cart = JSON.parse(cart);
                let product = JSON.parse(sessionStorage.getItem('product'));
                let key = product['tradeItemId'];
                let flag = true;
                for (const i in cart) {
                    if(key==i){
                        cart[i]['num']++;
                        flag = false;
                    }
                }
                if(flag){
                    product['num'] = 1;
                    cart[key] = product;
                }
                localStorage.setItem(uname,JSON.stringify(cart));
            }
        }
        ,
        ...mapActions(['loadComments'])
    },
    mounted(){
        let product = JSON.parse(sessionStorage.getItem("product"));
        this.img = product.img;
        this.title = product.title;
        this.price = product.price;
        this.oldprice = product.orgPrice;
        this.flag = product.price!=product.orgPrice;
        this.loadComments(product.tradeItemId);
    },
    components:{
        swiper, 
        swiperSlide
    }
}
</script>

<style lang="less" scoped>
    @import url('../../lessVar');
    .detail{
        position: absolute;
        top:0px;
        bottom: 0px;
        left: 0px;
        right: 0px;
        padding: 20px 20px;
        >.back{
            position: fixed;
            top:50px;
            left: 50px;
            font-size: 50px;
            z-index: 999;
            width: 80px;
            height: 80px;
            text-align: center;
            line-height: 70px;
            border: 1px solid #ccc;
            border-radius: 50%;
            color: white;
            background: rgba(0, 0, 0, 0.3)
        }
        >.wrapper{
            width: 100%;
            height: 100%;
            .wrapSwiper{
                width: 100%;
                img{
                    width: 100%;
                    height: auto;
                }
            }
        }
        .title{
            margin: 20px 10px;
            font-size: 40px;/*px*/
        }
        .price{
            span:first-child{
                position: relative;
                top:12px;/*px*/
                font-size: 30px;/*px*/
            }
            span:nth-child(2){
                vertical-align: top;
                font-size: 60px;/*px*/
                
            }
            >div{
                position: relative;
                vertical-align: top;
                display: inline-block;
                >.old{
                    font-size: 22px;/*px*/
                    color: #666;
                    text-decoration: line-through;
                }
                >.sale{
                    font-size: 28px;/*px*/
                    color: @colorful;
                    background: @bgcolor;
                }
            }
        }
        .btn{
            margin: 20px 10px;
            width: 98%;
            height: 80px;/*px*/
            vertical-align: middle;
            text-align: center;
            font-size: 36px;/*px*/
            background: @colorful;
            border-radius: 10px;
            color: white;
            outline: none;
        }
        .comment{
            padding: 10px 10px;
            >div{
                margin-bottom: 30px;
                padding-bottom: 40px;
                border-bottom: 1px solid #ccc;
            }
            .avatar{
                width: 100%;
                height: 100px;/*px*/
                img{
                    vertical-align: middle;
                    width: 80px;/*px*/
                    height: 80px;/*px*/
                    border-radius: 50%;
                    margin-right: 12px;/*px*/
                }
                span{
                    font-size: 30px;/*px*/
                }
            }
            .desc{
                span{
                    display: inline-block;
                    margin-right: 20px;
                    padding: 5px 10px;
                    background: @bgcolor;
                    color: @colorful;
                    margin-top: 10px;
                }
            }
        }
        
    }
</style>



