
import api from '../server/api'

const state = {
    topTypes : [],
    moreProducts:[]
}

const actions = {
    loadProductData({ commit }, { fcid,title,sort="pop"}){
        commit('setTypes', []);
        commit('setDatas', []);
        api.loadGuessData({
            cKey:16,
            page:1,
            sort,
            fcid,
            title
        }).then((data)=>{
            commit('setTypes',data.result.sortFilter);
            commit('setDatas',data.result.wall.docs);
        })
    },
    loadProductByTitle({ commit }, { q,sort="pop"}){
        commit('setTypes',[]);
        commit('setDatas',[]);
        api.loadGuessData({
            cKey:46,
            page:1,
            sort,
            q
        }).then((data)=>{
            commit('setTypes',data.result.sortFilter);
            commit('setDatas',data.result.wall.docs);
        })
    }
}

const mutations = {
    setDatas(state, data){
        state.moreProducts = data;
    },
    setTypes(state, data){
        state.topTypes = data;
    }
}

const getters = {

}

export default {
    state,
    actions,
    mutations,
    getters
}


