<template>
    <div>
        <nav-component></nav-component>
        <div class="classify">
            <!-- left scroll start -->
            <div class="leftScroll">
                <scroll-component class="wrapper">    
                    <div class="content">
                        <div class="types" v-for="(v,i) of type.leftTypes" :key="'leftData'+i" :class="{colorful:selectPid==v.maitKey}"  @click="changeType(v.maitKey,v.miniWallkey)">
                            <p>
                                {{ v.title }}
                            </p>
                        </div>
                    </div>
                </scroll-component>
            </div>
            <!-- left scroll end -->
            <!-- right scrll start -->
            <div class="rightScroll">
                <scroll-component class="wrapper" ref="rightcontent">  
                    <div class="content">
                        <div class="rightType">
                            <div v-for="(v,j) of type.rightTypes" :key="'rightData'+j" @click="searchData(v.link, v.title)">
                                <!-- <img :src="v.image" alt=""> -->
                                <img v-lazy="v.image" alt="">
                                <p>{{ v.title }}</p>
                            </div>
                        </div>
                        <div class="topType">
                            <ul>
                                <li v-for="(v,z) of type.topTypes" :key="'topData'+z" @click="sort(v.sortKey)" :class="{fontColor:v.sortKey==selectType}">{{ v.title }}</li>
                            </ul>
                            <div class="content">
                                <product-component  v-for="(v,m) of type.contentTypes" :key="'contentData'+m" :like="v">
                                    <p slot="title" class="title">{{ v.title }}</p>
                                    <div slot="price" class="price">￥{{ v.price }}</div>
                                    <div class="cfav" slot='cfav'>{{ v.cfav }}</div>            
                                </product-component>
                            </div>
                        </div>
                    </div>
                </scroll-component>
            </div>
            <!-- right scroll end -->
        </div> 
        <footer-component></footer-component>    
    </div>   
</template>

<script>

import BScroll from 'better-scroll'

import { mapState, mapActions, mapMutations, mapGetters } from "vuex";

export default {
    data(){
        return {
            selectPid:"41888",
            selectType:"pop",
        }
    },
    computed:{
        ...mapState(['type'])
    },
    methods:{
        changeType(id,miniKey){
            this.selectPid = id;
            this.selectType = "pop";
            this.loadRightType({id});
            this.loadTopTypes({miniKey,undefined});   
        },
        sort(key){
            let y = this.$refs.rightcontent.scroll.y;
            this.selectType = key;
            this.loadTopTypes({undefined, key,callback:()=>{
                setTimeout(() => {
                    this.$refs.rightcontent.scroll.scrollTo(0, y);
                }, 500);
                
            }});
            
        },
        searchData(link,title){
            let start = link.indexOf("?")+1;
            let end = link.indexOf("&");
            link = link.substring(start,end).split("=")[1];
            this.$router.push("/More/"+link+"/"+title);
        },
        ...mapActions(['loadLeftData', 'loadRightType', 'loadTopTypes'])
    },
    watch:{
        "type.typeId":function(){
            this.selectPid = this.type.typeId;
            this.loadRightType({id : undefined});
        },
        "type.currentId":function(){
            this.loadTopTypes({miniKey:undefined, sortKey:undefined});
        }
    },
    mounted(){
        this.loadLeftData();
    }
}
</script>

<style lang="less" scoped>
    @import url('../lessVar');
    
    .colorful{
        background: white;
        p{
            color: @colorful;
            border-left: 4px solid #FF5577;
        }
    }
    .fontColor{
        color: @colorful;
    }
    .classify{
        position: absolute;
        top: 100px;/*px*/
        bottom: 100px;
        left: 0px;
        right: 0px;
        display: flex;
        justify-content: flex-start;
        color: #666;
        .leftScroll{
            width: 30%;
            height: 100%;
            .wrapper{
                background: #f6f6f6;
                height: 100%;
                p{
                    font-size: 30px;/*px*/
                }
            }
        }
        .rightScroll{
            width: 70%;
            height: 100%;
            .wrapper{
                height: 100%;
                width: 100%;
                p{
                    font-size: 24px;/*px*/
                    margin: 20px 0px;/*px*/
                }
                .content{
                    width: 90%;
                    margin: 0 auto;
                    padding-top: 50px;/*px*/
                    .rightType{
                        width: 100%;
                        display: flex;
                        justify-content: flex-start;
                        text-align: center;
                        flex-wrap: wrap;
                        margin-bottom: 30px;/*px*/
                        >div{
                            width: 100%/3;
                            text-align: center;
                            >img{
                                width: 80%;
                                height: auto;
                            
                            }
                        }
                    }
                    .topType{
                        border-top: 1px solid #ccc;
                        border-bottom: 1px solid #ccc;
                        padding: 20px 0px;
                        ul{
                            display: flex;
                            justify-content: flex-start;
                            padding-bottom: 20px;
                            border-bottom: 1px solid #ccc;
                        }
                        ul>li{
                            text-align: center;
                            display: inline-block;
                            width: 100%/3;
                            border-left: 1px solid #ccc;
                        }
                        >.content{
                            padding-top: 20px;
                            width: 100%;
                            display: flex;
                            flex-wrap: wrap;
                            justify-content: space-around;
                            align-items: flex-end;
                            div{
                                width: 45%;
                                p{
                                    margin: 10px 0px;
                                    font-size: 24px;/*px*/
                                    overflow: hidden;
                                    line-height: 30px;
                                    height: 30px;
                                    position: relative;
                                    &::after{
                                        content: "...";
                                        position: absolute;
                                        bottom: 0px;
                                        right: 0px;
                                        font-size: 24px;
                                        line-height: 30px;
                                        background-color: white;
                                        font-weight: 900;
                                        padding-left:40px;
                                        padding-right: 10px;
                                        background:-webkit-linear-gradient(left,transparent,#fff 55%);
                                        background:-o-linear-gradient(right,transparent,#fff 55%);
                                        background:-moz-linear-gradient(right,transparent,#fff 55%);
                                        background:linear-gradient(to right,transparent,#fff 55%);
                                    }
                                }
                            }
                            .price{
                                display: inline-block;
                                color:@colorful;
                                font-size: 24px;/*px*/
                            }
                            .cfav{
                                text-align: right;
                                width: 36%;
                                padding-right: 40px;/*px*/
                                float: right;
                                font-size: 24px;
                                color: #999;
                                background: url(data:image/png;base64,) 100% no-repeat;
                                background-size: 20% 50%;
                                background-position: center right;
                            }
                        }
                    }
                }
            }
        }
        .types{
            height: 100px;/*px*/
            text-align: center;
            >p{
                position: relative;
                height: 40%;
                top:30%;
                bottom: 30%;
            }
        }
    }
</style>



