<template>
  <div class="scroll">
    <div class="body">
      <scroll-component class="wrapper">  
          <div class="content">
            <!-- swiper start -->
            <div class="wrapSwiper">
              <swiper :options="swiperOption">
                <swiper-slide class="swiper-slide" v-for="(v,i) of home.wraps" :key="'wrap'+i">
                  <img :src="v['image_800']" alt="">
                </swiper-slide>  
                <div class="swiper-pagination" slot="pagination"></div>     
              </swiper>
            </div>
            <!-- swiper end -->
            <!-- classify start -->
            <div class="module_class">
              <div v-for="(v,i) of home.classifys" :key="'class'+i">
                <div>
                  <img v-lazy="v.image" alt="">
                </div>
                <p>{{ v.title }}</p>
              </div>
            </div>
            <!-- classify end -->
            <!-- sale start -->
            <div class="sale">
              <div class="bg_img">
                <div>
                  超实惠-促销直达
                </div>
              </div>
              <div class="content">
                <div v-for="(v,i) of home.sales" :key="'sale'+i">
                  <div>
                    <p class="title">{{ v.title }}</p>
                    <p class="viceTitle">{{ v.viceTitle | showTitle }}</p>
                  </div>
                  <div>
                    <img v-lazy="v.image" alt="">
                  </div>
                </div>
              </div>
            </div>
            <!-- sale end -->
            <!-- hot classify start -->
            <div class="bg_img">
              <div>
                热门分类
              </div>
            </div>
            <div class="hotClassify">
              <div v-for="(v,i) of home.hots" :key="'hot'+i">
                <div>
                  <img :src="v.image" alt="">
                </div>
                <p>{{ v.title }}</p>
              </div>
            </div>
            <!-- hot classify end -->
            <!-- guess you like start -->
            <div class="bg_img">
              <div>
                猜你喜欢
              </div>
            </div>
            <div class="guess">
              <product-component v-for="(v,i) of home.likes" :key="i" :like="v">
                <p slot='title'>{{ v.title }}</p>
                <div slot='price' class="price">￥{{ v.price }}</div>
                <div class="cfav" slot='cfav'>{{ v.cfav }}</div>
              </product-component>
            </div>
            <!-- guess you like end -->
          </div>
        </scroll-component>
    </div>
  </div>
</template>
<script>

import BScroll from 'better-scroll'
import { swiper, swiperSlide } from "../../node_modules/vue-awesome-swiper";
import ProductComponent from "./global/ProductComponent"

import { mapState, mapActions, mapMutations, mapGetters } from 'vuex'

export default {
  name: 'HelloWorld',
  components:{
    swiper, 
    swiperSlide,
    ProductComponent,
    pagination:'.swiper-pagination'
  },
  data () {
    return {
      flag: true,
      swiperOption: {
        autoplay: 1000,
        loop:true,
        pagination:'.swiper-pagination',
        paginationClickable:true
      }
    }
  },
  computed:{
    ...mapState(['home'])
  },
  methods:{
    ...mapActions(['loadHomeData','loadLikesData']),
    changeFlag(){
      this.flag = !this.flag;
    }
  },
  mounted(){
    this.loadHomeData();
    this.loadLikesData();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import url('../lessVar');
  .scroll{
    width: 100%;
    height: 100%;
    >.body{
      position: absolute;
      top: 0px;/*px*/
      bottom: @bottomHeight*2;
      left: 0px;
      right: 0px;
      >.wrapper{
        width: 100%;
        height: 100%;
        font-size: 40px;/*px*/
        .wrapSwiper{
          width: 100%;
          .swiper-pagination {
            background: rgba(51, 51, 51, 0.4);
          }
          img{
            width: 100%;
            height: auto;
          }
        }
        .bg_img{
          width: 100%;
          text-align: center;
          >div{
            margin: 0 auto;
            text-align: center;
            width: 60%;
            font-size: 24px;/*px*/
            display: inline-block;
            background: url(https://s10.mogucdn.com/p1/160415/upload_ifrwkyzymu2damdgg4zdambqhayde_313x10.png) no-repeat;
            background-size: 100% 50%;
            background-position: center center;
          }
        }
        .module_class{
          text-align: center;
          display: flex;
          flex-wrap: wrap;
          justify-content: flex-start;
          >div{
            width: 20%;
            padding: 10px 0px;
            >div{
              margin: 0 auto;
              width: 100px;/*px*/
              height: 100px;/*px*/
              border-radius: 100px;/*px*/
              border: 1px solid #ccc;
              text-align: center;
              img{
                width: 100%;
                height: 100%;
              }
            }
            >p{
              margin-top: 10px;/*px*/
              font-size: 24px;/*px*/
            }
          }
        }
        .sale{
          margin-top: 20px;/*px*/
          width: 100%;
          text-align: center;
          .content{
            height: 600px;/*px*/
            margin-bottom: 20px;
            border-top: 1px solid #ccc;
            >div{
              float: left;
              box-sizing: border-box;
              width: 50%;
              height: 300px;/*px*/
              border-right: 1px solid #ccc;
              border-bottom: 1px solid #ccc;
              font-size: 28px;/*px*/
              .title{
                margin-top: 30px;
              }
              .viceTitle{
                color: @lightpink;
                font-size: 24px;/*px*/
              }
              &:nth-child(2),&:nth-child(3){
                height: 150px;/*px*/
                border-right: none;
                border-bottom: 1px solid #ccc;
                display: flex;
                justify-content: flex-start;
                >div:first-child{
                  width: 60%;
                  box-sizing: border-box;
                }
                >div:nth-child(2){
                  width: 40%;
                  box-sizing: border-box;
                }
              }
              &:nth-child(4){
                width: 100%/3;
                border-bottom: 1px solid #ccc;
                box-sizing: border-box;
              }
              &:nth-child(5){
                width: 100%/3;
                box-sizing: border-box;
              }
              &:nth-child(6){
                width: 100%/3;
                border-right: none;
                box-sizing: border-box;
              }
            }
          }
        }
        .hotClassify{
          margin: 20px 0px;/*px*/
          text-align: center;
          display: flex;
          flex-wrap: wrap;
          justify-content: flex-start;
          >div{
            width: 25%;
            padding: 10px 0px;
            >div{
              margin: 0 auto;
              width: 100px;/*px*/
              height: 100px;/*px*/
              border-radius: 100px;/*px*/
              border: 1px solid #ccc;
              >img{
                width: 100%;
                height: 100%;
              }
            }
            >p{
              margin-top: 10px;/*px*/
              font-size: 24px;/*px*/
            }
          }
        }
        .guess{
          width: 100%;
          margin-top: 20px;/*px*/
          display: flex;
          flex-wrap: wrap;
          justify-content: space-around;
          align-items: flex-end;
          p{
            margin: 10px 0px;
            font-size: 24px;/*px*/
            overflow: hidden;
            position: relative;
            line-height: 30px;
            height: 60px;
            &::after{
                content: "...";
                position: absolute;
                bottom: 0px;
                right: 0px;
                font-size: 24px;
                line-height: 30px;
                background-color: white;
                font-weight: 900;
                padding-left:40px;
                padding-right: 10px;
                background:-webkit-linear-gradient(left,transparent,#fff 55%);
                background:-o-linear-gradient(right,transparent,#fff 55%);
                background:-moz-linear-gradient(right,transparent,#fff 55%);
                background:linear-gradient(to right,transparent,#fff 55%);
            }
          }
          .price{
                display: inline-block;
            }
            .cfav{
                text-align: right;
                width: 40%;
                padding-right: 40px;/*px*/
                float: right;
                font-size: 24px;
                color: #999;
                background: url(data:image/png;base64,) 100% no-repeat;
                background-size: 16% 50%;
                background-position: center right;
            }
        }
      }
    }
  }
    
  
</style>




