import jsonpUtil from "../util/jsonpUtil";

const mce = "https://mce.mogucdn.com/jsonp/";
const search = "https://list.mogujie.com/search";
const comment = " https://rate.mogujie.com/h5/rate/jsonp/x6.rate.ratelist/v1";
const hotsearch = "https://list.mogujie.com/module/mget";

// const typeLeft = "https://mce.mogucdn.com/jsonp/multiget/3";

export default{
    // 数据的具体请求方法

    /**
     * 根据参数 pids 查询 相关数据
     * @param pids:string
     */

    loadDatasByPids(params){
        let url = mce+"multiget/3";
        return jsonpUtil.request(url, params);
    },
    loadGuessData(params){
        return jsonpUtil.request(search,params);
    },
    loadRightType(params){
        let url = mce+"makeup/3";
        return jsonpUtil.request(url,params);
    },
    loadComment(params){
        return jsonpUtil.request(comment, params);
    },
    loadHot(params){
        return jsonpUtil.request(hotsearch, params);
    }
}


