
import api from '../server/api'

const state = {
    wraps:[],
    classifys: [],
    sales: [],
    hots: [],
    likes: []
}

const actions = {
    loadHomeData({ commit }){
        commit("setWraps", []);
        commit("setClassifys", []);
        commit("setSales", []);
        commit("setHots", []);
        api.loadDatasByPids({
            appPlat: "m",
            pids: "51822,106930,51833,51836"
        }).then(({ data })=>{
            commit("setWraps", data[51822].list);
            commit("setClassifys", data[106930].list);
            commit("setSales", data[51833].list);
            commit("setHots", data[51836].list);

        })
    },
    loadLikesData({ commit }){
        commit("setLikes", []);
        api.loadGuessData({
            pid:"9750",
            sort:"pop",
            cKey:46
        }).then((data)=>{
            commit("setLikes", data.result.wall.docs);
        })
    }
}

const mutations = {
    setWraps(state,data){
        state.wraps = data;
    },
    setClassifys(state,data){
        state.classifys = data;
    },
    setSales(state,data){
        state.sales = data;
    },
    setHots(state,data){
        state.hots = data;
    },
    setLikes(state,data){
        state.likes = data;
    }
}

const getters = {

}

export default {
    state,
    mutations,
    actions,
    getters
}


