<template>
    <div class="login">
        <div>
            <label for="uname">username</label>
            <input type="text" id="uname" v-model="uname">
        </div>
        <div>
            <label for="upass">password</label>
            <input type="password" id="upass" v-model="upass">
        </div>
        <div>
            <input type="button" value="登录" class="btn" @click="login()">
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return {
            uname:'',
            upass:''
        }
    },
    methods:{
        login(){
            sessionStorage.setItem('user',this.uname);
            sessionStorage.setItem('pass',this.upass);
            sessionStorage.setItem('isLogin', true);
            let path = sessionStorage.getItem('backpath');
            this.$router.push(path);
        }
    }
}
</script>

<style lang="less" scoped>
    @import url('../lessVar');
    .login{
        width: 60%;
        margin: 30px auto;
        >div{
            margin-top: 30px;
            font-size: 30px;/*px*/
        }
        input{
            height: 50px;
            width: 60%;
            border: 2px solid #ccc;
            font-size: 30px;/*px*/
        }
        .btn{
            width: 100%;
            height: 60px;/*px*/
            font-size: 30px;/*px*/
            background: @colorful;
            color:white;
        }
    }
</style>



