import Vuex from 'vuex';
import Vue from 'vue';

import home from './home'
import type from './type'
import detail from './detail'
import more from './more'
import hot from './hot'

Vue.use(Vuex);

export default new Vuex.Store({
    modules:{
        home,
        type,
        detail,
        more,
        hot
    }
})


