<template>
    <div class="more">
        <nav-component :status='flag'></nav-component>
        <ul class="choose">
            <li v-for="(v,i) of more.topTypes" :key="'type'+i" @click="choose(v.sortKey)" :class="{ chooseType: v.sortKey==selectKey }">{{ v.title }}</li>
        </ul>
        <div class="noContent">
        </div>
        <div class="scroll">
        <scroll-component class="wrapper">    
            <div class="content">
                <div class="product">
                    <product-component v-for="(v,i) of more.moreProducts" :key="'project'+i" :like="v">
                        <div slot="title" class="title">
                            <div v-for="(prop,j) of v.props" :key="'prop'+j">
                                {{ prop }}
                            </div>
                        </div>
                        <div slot="price" class="price">￥{{ v.price }}</div>
                        <div class="cfav" slot='cfav'>{{ v.cfav }}</div>     
                    </product-component>
                </div>
            </div>
        </scroll-component>
        </div>
    </div>
</template>

<script>

import { mapState, mapMutations, mapActions, mapGetters } from 'vuex'

export default {
    data(){
        return {
            id:"",
            title:"",
            selectKey:"pop",
            swiperOption: {
                autoplay: 1000,
                loop:true
            },
            params:null,
            flag:true
        }
    },
    computed:{
        ...mapState(['more'])
    },
    methods:{
        choose(sort){
            this.selectKey = sort;
            if(this.params.area){
                this.loadProductData({ fcid:this.params.word, title: this.params.area, sort });
                return;
            }
            this.loadProductByTitle({ q:this.params.word,sort })
        },
        chooseOne(params){
            if(params.area){
                this.loadProductData({ fcid:params.word, title: params.area, sort:undefined });
                return;
            }
            this.loadProductByTitle({ q:params.word,sort:undefined })
        },
        ...mapActions(['loadProductData','loadProductByTitle'])
    },
    mounted(){
        this.params = this.$route.params;
        this.chooseOne(this.$route.params);
    },
    beforeRouteUpdate(to , from, next){
        this.flag = !this.flag;
        this.params = to.params;
        this.loadProductByTitle({ q:to.params.word,sort:undefined });
        next();
    }
}
</script>

<style lang="less" scoped>
    @import url('../lessVar');
    .chooseType{
        color: @colorful;
    }
    .more{
        >.choose{
            padding: 20px 0px;
            display: flex;
            justify-content: flex-start;
            border-top: 1px solid #ccc;
            border-bottom: 1px solid #ccc;
            >li{
                width: 100%/3;
                text-align: center;
                display: inline-block;
                border-left: 2px solid #ccc;
                font-size: 24px;/*px*/
            }
        }
        >.noContent{
            width: 100%;
            height: 20px;
            background: #f6f6f6;
        }
        
        >.scroll{
            position: absolute;
            top: @topHeight+95px;/*px*/
            bottom: 0px;
            left: 0px;
            right: 0px;
            background: #f6f6f6;
            >.wrapper{
                width: 100%;
                height: 100%;
                >.content{
                    >.product{
                        padding: 0px 24px;/*px*/
                        display: flex;
                        justify-content: space-between;
                        flex-wrap: wrap;
                        align-items: flex-end;
                        >div{
                            width: 48%;
                        }
                        >div:first-child{
                            margin: 0;
                        }
                        .title{
                            display: flex;
                            justify-content: flex-start;
                            flex-wrap: wrap;
                            align-items: flex-end;
                            height: 100px;
                            >div{
                                background: #dedede;
                                padding: 5px;
                                margin-bottom: 10px;
                                margin-right: 10px;
                            }
                        }
                        .price{
                            display: inline-block;
                            font-size: 30px;/*px*/
                        }
                        .cfav{
                            text-align: right;
                            width: 36%;
                            padding-right: 40px;/*px*/
                            float: right;
                            font-size: 30px;
                            color: #999;
                            background: url(data:image/png;base64,) 100% no-repeat;
                            background-size: 20% 60%;
                            background-position: center right;
                        }
                    }
                }
            }
        }
    }
</style>



